.search-container {
  text-align: center;
  background-color: rgba(0, 123, 255, .8);
  border-radius: 8px;
  margin-top: 20px;
  padding: 10px;
}

.search-form input[type="text"] {
  width: 100%;
  max-width: 400px;
  color: #000;
  background-color: rgba(255, 255, 255, .8);
  border: none;
  border-bottom: 2px solid #fff;
  outline: none;
  padding: 10px;
  font-size: 16px;
  transition: border-color .3s ease-in-out;
}

.search-form button[type="submit"] {
  color: #fff;
  cursor: pointer;
  background-color: rgba(0, 123, 255, .8);
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
  transition: background-color .3s ease-in-out;
}

.search-form button[type="submit"]:hover {
  background-color: rgba(0, 86, 179, .8);
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: rgba(0, 123, 255, .8);
  border: none;
  border-radius: 4px;
  margin: 20px auto;
  padding: 8px 16px;
  transition: background-color .3s ease-in-out;
  display: block;
}

.load-more:hover {
  background-color: rgba(0, 86, 179, .8);
}

.results-container {
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  gap: 16px;
  display: grid;
}

.photo-card {
  background-color: #fff;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 16px;
  transition: transform .2s ease-in-out;
  overflow: hidden;
  box-shadow: 0 2px 4px rgba(0, 0, 0, .1);
}

.photo-card:hover {
  transform: scale(1.02);
}

.photo-card img {
  width: 100%;
  height: auto;
}

.photo-card .info {
  margin-top: 12px;
  font-size: 14px;
}

.photo-card .info-item {
  margin: 4px 0;
}

